import {LitElement, html, css} from 'lit';
import {customElement, property, state} from 'lit/decorators.js';
import {ref, createRef, RefOrCallback} from 'lit/directives/ref.js';

import {MForm} from './ui/index.js';
import './ui/index.js';

import {model} from './ui/utils/ModelDirective';

/**
 */
@customElement('my-element')
export class MyElement extends LitElement {
  @state()
  protected _interest = 0;

  @state()
  protected _mortgageType = 'annuiteiten';

  @state()
  protected _inflation = 0;

  /**
   * Percentage of house price, measured from today, based on selected predictionStrategy
   */
  @property({type: Number})
  housePrice = 0;

  /**
   * Can be 'monetarist' or 'keynesian'
   * Depending on strategy chosen, a different inflation rate and house price will be chosen
   */
  @property()
  school = 'keynesian';

  protected refs: {[key: string]: RefOrCallback} = {
    form: createRef<MForm>(),
  };

  private __schools = ['keynesian', 'monetarist'];
  private __durations = [20, 30];

  static styles = css`
    :host {
      display: block;
      border: solid 1px gray;
      padding: 16px;
      max-width: 800px;
    }
  `;

  @state()
  protected _model = {
    housePrice: 350000,
    currentRent: 10000,
    mortgageDuration: 30,
    economicSchool: 'monetarist',
    interest: 1.51,
  };

  render() {
    return html`
      <m-form ${ref(this.refs.form)} ${model(this._model)}>
        <form style="display: flex;">
          <m-input-amount
            step="1000"
            label="House Price"
            name="housePrice"
            help-text="What's the price of the house that you want to buy?"
          ></m-input-amount>

          <m-input-amount
            step="10"
            label="Current Rent"
            name="currentRent"
            help-text="How much rent do you pay monthly for your current residence?"
          ></m-input-amount>

          <m-input-number
            step="0.01"
            label="Interest Rate Mortgage"
            name="interest"
            help-text="What's the interest rate belonging to your mortgage?"
          ></m-input-number>

          <m-select-rich
            label="Mortgage Duration (years)"
            name="mortgageDuration"
            >${this.__durations.map(
              (duration) => html`
                <m-option .choiceValue="${duration}">${duration}</m-option>
              `
            )}
          </m-select-rich>

          <m-select-rich label="Economic School" name="economicSchool">
            ${this.__schools.map(
              (school) => html`
                <m-option .choiceValue="${school}">${school}</m-option>
              `
            )}
          </m-select-rich>
        </form>
      </m-form>
      ${JSON.stringify(this._model)}
      <m-chart></m-chart>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'my-element': MyElement;
  }
}
